/* --------------------------------
 Smart fixed navigation
 -------------------------------- */
.nucleo-icons-page-styles {
  /* mobile first */
}
.nucleo-icons-page-styles #cd-nav ul {
  position: fixed;
  width: 90%;
  max-width: 400px;
  max-height: 90%;
  right: 5%;
  bottom: 5%;
  border-radius: 0.25em;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background: white;
  visibility: hidden;
  /* remove overflow:hidden if you want to create a drop-down menu - but then remember to fix/eliminate the list items animation */
  overflow: hidden;
  overflow-y: auto;
  z-index: 1;
  /* Force Hardware Acceleration in WebKit */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: scale(0);
  transform: scale(0);
  -webkit-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
  transition: visibility 0s 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, visibility 0s 0.3s;
  transition: transform 0.3s, visibility 0s 0.3s, -webkit-transform 0.3s;
}
.nucleo-icons-page-styles #cd-nav ul li {
  /* Force Hardware Acceleration in WebKit */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  display: block;
  width: 100%;
  padding: 0;
  text-align: left;
}
.nucleo-icons-page-styles #cd-nav ul li:hover {
  background-color: #fff;
}
.nucleo-icons-page-styles #cd-nav ul.is-visible {
  visibility: visible;
  -webkit-transform: scale(1);
  transform: scale(1);
  transition: visibility 0s 0s, -webkit-transform 0.3s;
  transition: transform 0.3s, visibility 0s 0s;
  transition: transform 0.3s, visibility 0s 0s, -webkit-transform 0.3s;
}
.nucleo-icons-page-styles #cd-nav li a {
  display: block;
  padding: 1em;
  font-size: 1.2rem;
  border-bottom: 1px solid #eff2f6;
}
.nucleo-icons-page-styles #cd-nav li a:hover {
  color: #333;
}
.nucleo-icons-page-styles #cd-nav li:last-child a {
  border-bottom: none;
}
.nucleo-icons-page-styles .cd-nav-trigger {
  position: fixed;
  bottom: 5%;
  right: 5%;
  width: 44px;
  height: 44px;
  background: white;
  border-radius: 0.25em;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  /* image replacement */
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
  z-index: 2;
}
.nucleo-icons-page-styles .cd-nav-trigger span {
  /* the span element is used to create the menu icon */
  position: absolute;
  display: block;
  width: 20px;
  height: 2px;
  background: #333;
  top: 50%;
  margin-top: -1px;
  left: 50%;
  margin-left: -10px;
  transition: background 0.3s;
}
.nucleo-icons-page-styles .cd-nav-trigger span::before, .nucleo-icons-page-styles .cd-nav-trigger span::after {
  content: '';
  position: absolute;
  left: 0;
  background: inherit;
  width: 100%;
  height: 100%;
  /* Force Hardware Acceleration in WebKit */
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: background 0s, -webkit-transform 0.3s;
  transition: transform 0.3s, background 0s;
  transition: transform 0.3s, background 0s, -webkit-transform 0.3s;
}
.nucleo-icons-page-styles .cd-nav-trigger span::before {
  top: -6px;
  -webkit-transform: rotate(0);
  transform: rotate(0);
}
.nucleo-icons-page-styles .cd-nav-trigger span::after {
  bottom: -6px;
  -webkit-transform: rotate(0);
  transform: rotate(0);
}
.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open {
  box-shadow: none;
}
.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span {
  background: rgba(232, 74, 100, 0);
}
.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span::before, .nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span::after {
  background: #333;
}
.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span::before {
  top: 0;
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
}
.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span::after {
  bottom: 0;
  -webkit-transform: rotate(225deg);
  transform: rotate(225deg);
}
.nucleo-icons-page-styles .now-ui-icons {
  display: inline-block;
