/* ----- image defaults ----- */
.art-img-default {
  width: 100%;
  box-shadow: gray;
}

/* ----- link defaults ----- */
a {
  text-decoration: none;
  color: #b3003b;

  &:hover {
    text-decoration: none;
    color: white;
  }

  &:focus {
    outline: inherit;
  }
}
/* ----- table ----- */
.art-table {
  color: #8c8c8e;

  & ul {
    padding: 0;
    margin: 0;

    & li {
      list-style-type: none;
      display: flex;
      justify-content: space-between;
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
/* ----- quote ----- */
blockquote {
  margin-top: 30px;
  margin-bottom: 30px;
  background: forestgreen;
  color: red;
  padding: 30px;
  padding-left: 35px;
  font-weight: 400;
  font-style: italic;
  font-size: 14px;
  box-shadow: gray;
  position: relative;

  &:before {
    position: absolute;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f10d";
    font-size: 18px;
    opacity: .5;
    top:5px;
    left: 9px;
    color: pink;
  }

  &:after {
    position: absolute;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f10d";
    font-size: 18px;
    opacity: .5;
    bottom:5px;
    right: 9px;
    color: firebrick;
    transform: rotate(180deg);
  }
}
/* ----- list ----- */
.art-custom-list {
  padding-left: 25px;

  & li {
    position: relative;
    list-style-type: none;
    color: purple;
    margin-bottom: 5px;

    &:before {
      content: '';
      position: absolute;
      top: 4px;
      left: -25px;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      border: solid 2px purple;
      background: #24242f;
      box-shadow: gray;
    }
  }
}
/* ------------------------------

link

------------------------------ */
.art-link {
  position: relative;
  text-transform: uppercase;
  font-size: 10px;
  display: inline-block;
  letter-spacing: 1.5px;
  font-weight: 600;
  margin-right: 15px;
  margin-bottom: 10px;
  color: lightgray;
  transition: 0.4s ease-in-out;

  &:hover {
    color: rgba(139, 23, 33, 0.933);
    // text-shadow: $text-neon;
  }

  &.art-white-link {
    color: pink;

    &:hover {
      color: white;
    //   text-shadow: $text-neon;
      transform: translateX(5px);
    }
  }

  &.art-color-link {
    color: #b3003b;

    &:hover {
    //   color: $main-color;
    //   text-shadow: $text-neon;
      transform: translateX(5px);
    }
  }

  &.art-w-chevron {
    padding-right: 10px;
    &:after {
      position: absolute;
      font-family: "Font Awesome 5 free";
      content: '\f054';
      font-weight: 900;
      display: inline-block;
      font-style: normal;
      font-variant: normal;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
      font-size: 9px;
      top: 0.3px;
      transform: translateX(5px);
    //   transition: $tr-md;
    }

    &:hover {
      transform: translateX(0);

      &:after {
        transform: translateX(10px);
      }
    }
  }

  &:last-child {
    margin-bottom: 0;
    margin-right: 0;
  }

  & i {
    font-size: 9px;
    margin-left: 3px;
  }

  &.art-left-link {
    padding-left: 10px;
    padding-right: 0;

    &.art-w-chevron {
      &:after {
        content: '\f053';
        left: 0;
        transform: translateX(0);
      }

      &:hover {
        &:after {
          transform: translateX(-5px);
        }
      }
    }
  }
}
/* ------------------------------

button

------------------------------ */
.art-btn {
  text-transform: uppercase;
  box-shadow: gray;
  color: #b3003b;
  background: #24242f;
  letter-spacing: 1.5px;
  font-weight: 600;
  display: inline-flex;
  justify-content: center;
  align-content: center;
  border: none;
  transition: 0.4s ease-in-out;

  & span {
    align-self: center;
    // transition: $tr-sm;
  }

  &.art-btn-md {
    height: 45px;
    font-size: 11px;
    padding: 0 35px;
  }

  &.art-btn-sm {
    height: 40px;
    font-size: 11px;
    padding: 0 30px;
  }

  &:hover {
    transform: translateY(-2px);
    color: gray;
  }
}
